@media (min-width: 1200px) {
  :root {
    font-size: calc(1.56522px + 0.86957vw);
  }
}

@media (min-width: 1430px) {
  :root {
    font-size: 14px;
  }

  .container {
    max-width: 1430px;
  }
}

@media (max-width: 1199px) {
  :root {
    font-size: 12px;
  }
}


