:global :root {
  /*// набор пропорция для вертикального ритма*/
  --rem03: 4.8px;
  --rem03: 0.3rem; /*//малый border-radius, некоторые технические вещи вроде подчеркиваний */
  --rem05: 8px;
  --rem05: 0.5rem; /*//большой border-radius, горизонтальные отступы между иконкой и текстом, некоторые отступы между шрифтами разного размера в заголовках*/
  --rem08: 12.8px;
  --rem08: 0.8rem; /*// малый размер шрифта (теги, даты постов, подписи и.т.д.)*/
  --rem093: 14.864px;
  --rem093: 0.929rem;
  --rem1: 16px;
  --rem1: 1rem; /*// эталонный шрифт, вертикальный и горизонтальный отступ по умолчанию для всего, что только есть (включая отступы в сетке, разумеется)*/
  --rem114: 18.288px;
  --rem114: 1.143rem; /*// слегка увеличенный шрифт (хлебные крошки в шипке)*/
  --rem125: 20px;
  --rem125: 1.25rem; /*// увеличенный шрифт*/
  --rem15: 24px;
  --rem15: 1.5rem; /*// увеличенный шрифт для больших заголовков*/
  --rem175: 28px;
  --rem175: 1.75rem; /*// увеличенный шрифт для больших заголовков (заменяет 1.5 в случае некоторых шрифтов)*/
  --rem2: 32px;
  --rem2: 2rem; /*// большой шрифт, большие отступы во всех направлениях*/
  --rem3: 48px;
  --rem3: 3rem; /*// большой вертикальный отступ*/
  --rem4: 64px;
  --rem4: 4rem; /*// большой отступ между секциями*/
  --rem5: 80px;
  --rem5: 5rem; /*//  большой-большой отступ между секциями (имеет смысл в лендингах, когда на стыке секций в верхней отступ делается больше чем в нижней)*/

  /*
      Единицы измерений
  */
  /*// значения при размере экрана >= 1430 (можно попробовать заменить круглыми близкими значениями из набора пропорций)*/
  --px24: var(--rem175);
  --px24: var(--rem175);
  --px22: 25.136px;
  --px22: 1.571rem;
  --px18: var(--rem125);
  --px18: var(--rem125);
  --px32: var(--rem2);
  --px32: var(--rem2);
  --px12: var(--rem08);
  --px12: var(--rem08);
  --px13: var(--rem093);
  --px13: var(--rem093);
  --px16: var(--rem114);
  --px16: var(--rem114);
  --px11: 12.576px;
  --px11: 0.786rem;

  --input-height: 41.136px;
  --input-height: 2.571rem; /*// 36*/

  --standart-space: 34.288px;
  --standart-space: 2.143rem; /*// 30*/
  --small-space: 26.288px;
  --small-space: 1.643rem; /*// 23*/
  --inputs-space: 22.864px;
  --inputs-space: 1.429rem; /*// 20*/
  --min-space: 11.424px;
  --min-space: 0.714rem; /*// 10*/
  --inputs-height: 97.142px;
  --inputs-height: 6.0714rem; /*// 85px*/
  --inputs-margin: 30.864px;
  --inputs-margin: 1.929rem; /*// 27*/
  --mega-space: 57.136px;
  --mega-space: 3.571rem; /*//  50*/
  --mega-space-plus: 74.384px;
  --mega-space-plus: 4.649rem; /*//  65*/
  --small-row-space: 20.576px;
  --small-row-space: 1.286rem; /*// 18*/


  --icon-text-gap: 8px;
  --icon-text-gap: 0.5rem; /*// 7px*/

  --navigation: 114.288px;
  --navigation: 7.143rem; /*// 100px*/

  --navigation-opened: 285.712px;
  --navigation-opened: 17.857rem; /*// 250px*/
  --standard-grid-col: 348.576px;
  --standard-grid-col: 21.786rem; /*// 305px*/
  --min-button-width: 258.288px;
  --min-button-width: 16.143rem;

  --amount-font: 27.424px;
  --amount-font: 1.714rem; /*// 24px*/

  --initial-title-font: 25.136px;
  --initial-title-font: 1.571rem;
}


