/**
 * The geosuggest module
 * NOTE: duplicated font-sizes' are for browsers which don't support rem (only IE 8)
 */
.geosuggest {
  font-size: 18px;
  font-size: 16px;
  font-size: 1rem;
  position: relative;
  width: 50%;
  /*margin: 1em auto;*/
  text-align: left;
}
.geosuggest:after {
  content: "";
  display: block;
  visibility: hidden;
  height: 0;
  width: 0;
  border-top: 11.424px solid transparent;
  border-top: 0.714rem solid transparent;
  border-bottom: 11.424px solid transparent;
  border-bottom: 0.714rem solid transparent;
  border-left: 11.424px solid transparent;
  border-left: 0.714rem solid transparent;
  border-right: 11.424px solid #12295e;
  border-right: 0.714rem solid #12295e;
  position: absolute;
  top: 50%;
  /*left: 100%;*/
  right: -11.424px;
  right: -0.714rem;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  opacity: .3;
  -webkit-transition: opacity .4s;
  transition: opacity .4s;
}
.geosuggestActive:after {
  visibility: visible;
  opacity: 1;
}
.geosuggest__input {
  margin-top: -1px;
  height: 41.136px;
  height: 2.571rem;
  outline: none;
  width: 100%;
  border: none;
  background-color: #ffffff   ;
  background-color: rgba(255, 255, 255, 0) /*border: 2px solid transparent;
  box-shadow: 0 0 1px #3d464d;*/ /* padding: .5em 1em; */ /* -webkit-transition: border 0.2s, box-shadow 0.2s;
          transition: border 0.2s, box-shadow 0.2s; */;
}
.geosuggest__input:focus {
  outline: none;
  /*border-color: #267dc0;
  box-shadow: 0 0 0 transparent;*/
}
.geosuggest__suggests {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  max-height: 25em;
  padding: 0;
  margin-top: 1px;
  background: #fff;
  border: solid 1px #dfe3e9;
  /*border: 1px solid #267dc0;*/
  /*border-top-width: 0;*/
  overflow-x: hidden;
  overflow-y: auto;
  list-style: none;
  z-index: 5;
  -webkit-transition: max-height 0.2s, border 0.2s;
  transition: max-height 0.2s, border 0.2s;
}
.geosuggest__suggests--hidden {
  max-height: 0;
  overflow: hidden;
  border-width: 0;
}
/**
 * A geosuggest item
 */
.geosuggest__item {
  font-size: 18px;
  font-size: 16px;
  font-size: 1rem;
  padding: .5em .65em;
  cursor: pointer;
  color: black;
}
.geosuggest__item:hover,
.geosuggest__item:focus {
  background: #f5f5f5;
}
.geosuggest__item--active {
  background: #267dc0;
  color: #fff;
}
.geosuggest__item--active:hover,
.geosuggest__item--active:focus {
  background: #ccc;
}
.geosuggest__item__matched-text {
  font-weight: bold;
}



