.dropdown {
  display: inline-block;
}
.dropdown__content {
  display: none;
  position: absolute;
  left: 57%;
  top: 68%;
  text-align: left;
  -webkit-box-shadow: 0 1px 10px 0 rgba(0, 0, 0, .1);
          box-shadow: 0 1px 10px 0 rgba(0, 0, 0, .1);
  z-index: 2;
}
@-webkit-keyframes fadeIn {
  0% {
    width: 0rem;
    opacity: 0;
  }
  70% {
    width: 0rem;
    opacity: 0;
  }
  100% {
    width: 11rem;
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    width: 0rem;
    opacity: 0;
  }
  70% {
    width: 0rem;
    opacity: 0;
  }
  100% {
    width: 11rem;
    opacity: 1;
  }
}
.dropdown-content__field {
  line-height: 41.12px;
  line-height: 2.57rem;
  height: 41.12px;
  height: 2.57rem;
  width: 320px;
  width: 20rem;
  font-weight: 500;
  padding: 0 11.2px;
  padding: 0 0.7rem;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  font-size: 14.864px;
  font-size: 0.929rem;
  letter-spacing: 0.1px;
  cursor: pointer;
  border-bottom: 1px solid #e6eaee;
  background-color: #ffffff;
}
.dropdown-content__field:hover {
  background-color: #f9f9f9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.dropdown--active .dropdown__content {
  display: block;
}



